.portfolio {
  padding-block: 50px 103px;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
  transform: translateY(-32px);
}
.portfolio__body {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 24px;
}
.portfolio__image {
  margin-inline: auto;
}
.portfolio__slider {
  display: none;
}

@media (min-width: 912px){
  .portfolio__image {
    filter: grayscale(100);
  }
  .portfolio__image:hover {
    filter: grayscale(0);
  }
}

@media (max-width: 911px) {
  .portfolio__body {
    grid-template-columns: 1fr 1fr;
  }
  .portfolio__image-1 { content: url("../../assets/images/portfolio/horizontal/image-1.png") }
  .portfolio__image-2 { content: url("../../assets/images/portfolio/horizontal/image-2.png") }
  .portfolio__image-3 { content: url("../../assets/images/portfolio/horizontal/image-3.png") }
  .portfolio__image-4 { content: url("../../assets/images/portfolio/horizontal/image-4.png") }
  .portfolio__image-5 { content: url("../../assets/images/portfolio/horizontal/image-5.png") }
  .portfolio__image-6 { content: url("../../assets/images/portfolio/horizontal/image-6.png") }
  .portfolio__image-7 { content: url("../../assets/images/portfolio/horizontal/image-7.png") }
  .portfolio__image-8 { content: url("../../assets/images/portfolio/horizontal/image-8.png") }
  .portfolio__image-9 { content: url("../../assets/images/portfolio/horizontal/image-9.png") }
  .portfolio__image-10 { content: url("../../assets/images/portfolio/horizontal/image-10.png") }
}
@media (max-width: 600px) {
  .portfolio__image-1 { content: url("../../assets/images/portfolio/vertical/image-1.png") }
  .portfolio__image-2 { content: url("../../assets/images/portfolio/vertical/image-2.png") }
  .portfolio__image-3 { content: url("../../assets/images/portfolio/vertical/image-3.png") }
  .portfolio__image-4 { content: url("../../assets/images/portfolio/vertical/image-4.png") }
  .portfolio__image-5 { content: url("../../assets/images/portfolio/vertical/image-5.png") }
  .portfolio__image-6 { content: url("../../assets/images/portfolio/vertical/image-9.png") }
  .portfolio__image-7 { content: url("../../assets/images/portfolio/vertical/image-7.png") }
  .portfolio__image-8 { content: url("../../assets/images/portfolio/vertical/image-8.png") }
  .portfolio__image-9 { content: url("../../assets/images/portfolio/vertical/image-6.png") }
  .portfolio__image-10 { content: url("../../assets/images/portfolio/vertical/image-10.png") }
}
@media (max-width: 498px) {
  .section-header {
    font-size: var(--font-size-600);
  }
  .portfolio__image {
    display: none;
  }
  .portfolio__slider, .portfolio__body {
    display: block;
  }
  .portfolio {
    padding-bottom: 75px;
  }
}
@media (max-width: 300px) {
  .portfolio__header {
    display: none;
  }
}
