.qna {
  padding-block: 120px 100px;
}
.qna__body {
  list-style: none;
}
.qna__body li {
  padding-bottom: 11px;
  border-bottom: 1px solid var(--color-neutral-900);
}
.qna__question {
  display: flex;
  justify-content: space-between;
  gap: 24px;
  padding-block: 22px 11px;
  cursor: pointer;
  user-select: none; //selects onclick on mobile
}
.drop-down-arrow {
  min-width: 19px;
  transform: translateY(50%);
}

.qna__checkbox {
  display: none;
}
.qna__checkbox:checked ~ .qna__question {
  .drop-down-arrow {
    transform: translateY(50%) rotate(180deg);
  }
}

.qna__answer {
  p {
    line-height: 140%;
    overflow: hidden;
  }
  display: grid;
  grid-template-rows: 0fr;
  transition: grid-template-rows 0.4s;
}

.qna__checkbox:checked ~ .qna__answer {
  grid-template-rows: 1fr;
}

@media (max-width: 911px) {
  .qna__question {
    font-size: var(--font-size-400);
  }
  .qna__answer p {
    font-size: var(--font-size-300);
  }
}
@media (max-width: 498px) {
  .qna__question {
    font-size: var(--font-size-300);
  }
  .qna__answer p {
    font-size: var(--font-size-200);
  }
}
@media (max-width: 270px) {
  .qna__question {
    gap: 0;
  }
}
