.welcome {
    background-image: url('../../assets/images/welcome/sonya-welcome-1440.png');
    background-position: center;
    max-width: 1440px;
    min-height: 836px;
    margin-inline: auto;
}

.welcome__header {
    padding-top: 32px;
    display: flex;
    justify-content: space-between;
    gap: 56px;
}
.burger-menu {
    display: none;
}
.nav-list {
    display: flex;
    max-width: 592px;
    flex-grow: 1;
    justify-content: space-between;
    list-style-type: none;
}

.hero {
    margin-top: 150px;
    position: relative;
    overflow: hidden;
}
.hero__subtitle {
    position: absolute;
    left: 381px;
    top: 90px;
    stop {
        transition: 0.4s ease;
    }
}
.hero__subtitle:hover stop:first-child {
    stop-color: #AC2F13;
}
.hero__subtitle:hover stop:last-child {
    stop-color: #F6411A;
}

.hero__text {
    padding-block: 29px 43px;
}

@media (max-width: 1093px) {
    .welcome {
        background-image: url('../../assets/images/welcome/sonya-welcome-1080.png');
    }
    .welcome__header {
        justify-content: right;
    }
    .logo {
        display: none;
    }
}
@media (max-width: 953px) {
    .welcome {
        background-position: center;
    }
}
@media (max-width: 911px) {
    .welcome {
        background-image: url('../../assets/images/welcome/sonya-welcome-911.png');
        background-position: right;
    }
    .hero {
        margin-top: 30px;
    }
    .hero__title {
        max-width: 397px;
    }
    .hero__subtitle {
        max-width: 200px;
        left: 303px;
        top: 67px;
    }
}
@media (max-width: 698px) {
    .logo, .nav-list {
        display: none;
    }
    .burger-menu {
        display: block;
    }
}
@media (max-width: 449px) {
    .cta-button {
        width: min(264px, 100%);
    }
}
@media (max-width: 303px) {
    .cta-button {
        font-size: var(--font-size-300);
    }
}
@media (max-width: 277px) {
    .cta-button {
        font-size: var(--font-size-200);
    }
}
